.modals {
  position: fixed;
  z-index: 20;

  &__login {
    position: fixed;
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    padding: 0 3vw;
    background-color: $gray;
  }

  &__login_open {
    animation: loginOpen 0.3s forwards;
  }

  &__login_close {
    animation: loginClose 0.3s forwards;
  }

  &__login-form {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: max-content;
    min-width: 20vw;
    height: max-content;
    min-height: 20vh;
    padding: 30px;
    padding-top: 50px;
    background-color: $bgWhite;
    border-radius: 10px;
  }

  &__login-input {
    padding: 10px;
    margin-bottom: 15px;
    font-size: 18px;
    border: 1px solid $black;
    border-radius: 5px;
  }

  &__login-text {
    font-size: 24px;
    font-weight: 600;
    line-height: 42px;
  }

  &__login-btn {
    display: inline-block;
    padding: 10px;
    margin: 10px 0;
    font-size: 18px;
  }

  &__login-btn:hover {
    color: $blue;
    background-color: $bgGray;
    box-shadow: 4px 4px 8px 0 rgba(50, 135, 201, 0.2);
  }

  &__login-btn:last-child {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 32px;
    height: 32px;
    padding: 0;
    font-size: 14px;
    line-height: 32px;
    border: 1px solid rgb(206, 56, 56);
    border-radius: 5px;
  }

  &__login-btn:last-child:hover {
    color: rgb(219, 20, 20);
    border: 2px solid rgb(219, 20, 20);
  }
}
