@import 'reset';
@import 'fonts';
@import '_variables.scss';
@import '_animations.scss';
@import '_appbar.scss';
@import '_account.scss';
@import '_top-nav.scss';
@import '_board-sales.scss';
@import '_departments.scss';
@import '_products.scss';
@import '_rating.scss';
@import '_product.scss';
@import '_modals.scss';
@import '_footer.scss';

body {
  font-family: 'Inter', sans-serif;
  background-color: $bgWhite;
}

.hidde {
  display: none;
}

.page {
  display: flex;
  flex-shrink: 0;
  justify-content: space-between;
  width: 100%;
  padding: 0 3%;
}

use {
  display: block;
  width: 100%;
  height: 100%;
}

.stopScroll {
  overflow: hidden;
}

.swiper-button-prev {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 100%;
  background-image: url('../images/arrowRight.svg');
  background-repeat: no-repeat;
  background-position: center;
}

.swiper-button-next {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 100%;
  background-image: url('../images/arrowLeft.svg');
  background-repeat: no-repeat;
  background-position: center;
}

.swiper-button-prev::after {
  display: none;
}

.swiper-button-next::after {
  display: none;
}

.not-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 70%;
  color: $black;
  text-align: center;

  &__title {
    font-size: 15vw;
    font-weight: 600;
  }

  &__text {
    padding: 20px;
    font-size: 3vw;
    line-height: 1.5em;
  }
}
